<!--  -->
<template>
  <div>
    <el-upload
      class="avatar-uploader"
      :show-file-list="false"
      action=""
      :auto-upload="false"
      :on-change='changeUpload'>
      <img v-if="imageUrl" :src="this.url + '/image/' + imageUrl + '?token=' + token" class="avatar">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
    <el-dialog title="图片剪裁" :visible.sync="dialogVisible" append-to-body>
      <div class="cropper-content">
        <div class="cropper" style="text-align:center">
        <vueCropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="true"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :centerBox="option.centerBox"
            :infoTrue="option.infoTrue"
            :fixedBox="option.fixedBox"
            @realTime="realTime"
          ></vueCropper>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="finish" :loading="loading">确认</el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';
// import {getSoundUrl} from '@/http/AppApi'
import {getToken} from '@/utils/LocalDataUtil'
import config from '@/config/index'
export default {
  model: {
    prop: 'imageUrl',
    event: 'updateImgUrl'
  },
  props: {
    imageUrl: {
      type: String
    }
  },
  // import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    // 这里存放数据
    return {
      fileName: '',
      url: config.serverUrl(),
      dialogVisible: false,
      token: getToken(),
      previews: {},
      // 裁剪组件的基础配置option
      option: {
        img: '', // 裁剪图片的地址
        info: true, // 裁剪框的大小信息
        outputSize: 0.8, // 裁剪生成图片的质量
        outputType: 'jpeg', // 裁剪生成图片的格式
        canScale: false, // 图片是否允许滚轮缩放
        autoCrop: true, // 是否默认生成截图框
        autoCropWidth: 150, // 默认生成截图框宽度
        autoCropHeight: 150, // 默认生成截图框高度
        fixedBox: true, // 固定截图框大小 不允许改变
        fixed: true, // 是否开启截图框宽高固定比例
        fixedNumber: [1, 1], // 截图框的宽高比例
        full: true, // 是否输出原图比例的截图
        canMoveBox: false, // 截图框能否拖动
        original: false, // 上传图片按照原始比例渲染
        centerBox: false, // 截图框是否被限制在图片里面
        infoTrue: true // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
      },
      picsList: [], // 页面显示的数组
      // 防止重复提交
      loading: false
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    changeUpload (file, fileList) {
      this.fileName = file.name
      let _this = this
      const isLt5M = file.size / 1024 / 1024 < 5
      if (!isLt5M) {
        this.$message.error('上传文件大小不能超过 5MB!')
        return false
      }
      // let imageUrl = URL.createObjectURL(file.raw)
      let reader = new FileReader()
      reader.readAsDataURL(file.raw)
      reader.onload = function (event) {
        let imageUrl = this.result
        // _this.$emit('updateImgUrl', imageUrl)
        _this.fileinfo = file
        _this.dialogVisible = true
        _this.option.img = imageUrl
      }

      // 上传成功后将图片地址赋值给裁剪框显示图片

      setTimeout(() => {
      }, 1000)
    },
    // 实时预览
    realTime (data) {
      this.previews = data
    },
    finish () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      var formData = new FormData()
      this.$refs.cropper.getCropBlob(res => {
        // res是裁剪后图片的bolb对象
        // formData.append('file', res, this.userId)
        // res = res + this.postfix
        formData.append('files', res, this.fileName)
        // getSoundUrl(formData).then(resp => {
        //   if (resp.code && resp.code === '200') {
        //     let data = resp.data
        //     this.$emit('updateImgUrl', data[0])
        //     this.dialogVisible = false
        //     loading.close()
        //   }
        // })
      })
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () {}, // 生命周期 - 创建之前
  beforeMount () {}, // 生命周期 - 挂载之前
  beforeUpdate () {}, // 生命周期 - 更新之前
  updated () {}, // 生命周期 - 更新之后
  beforeDestroy () {}, // 生命周期 - 销毁之前
  destroyed () {}, // 生命周期 - 销毁完成
  activated () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
.avatar-uploader >>> .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader >>> .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .cropper-content .cropper {
      width: auto;
      height: 400px;
}

</style>



