import req from '@/http/http'


//----------------------------------------------------------

//获取组织机构树
export const getAllDeptTree = data => {
  let path = `/sys/org/selectOrgTrees`
  return req.post(path, data)
}

//获取左侧菜单
export const getSlideMenu = data => {
  let path = `/sys/menu/selectChild`
  return req.post(path, data)
}

// 获取菜单
export const findUserPermissionTree = param => {
  let path = `/sys/menu/selectAll`
  return req.post(path, param)
}

// 登录接口
export const login = data => {
  let path = `/login?loginName=${data.loginName}&password=${data.password}`
  return req.post(path)
}

// 登出接口
export const loginOut = () => {
  let path = `/logout`
  return req.post(path)
}

//获取个人信息
export const getPersonInfo = data => {
  let path = '/person/info'
  return req.post(path, data)
}

//修改密码
export const changePwd = data => {
  let path = '/person/changePwd'
  return req.post(path, data)
}



