import req from '@/http/http'

//-------------------会议信息-----------------------//
//添加党组织
export const addPartyDept = data => {
  let path = `/sys/org/add`
  return req.post(path, data)
}
//删除党组织
export const delPartyDept = data => {
  let path = `/sys/org/delete`
  return req.post(path, data)
}
//获取党组织详情
export const getPartyDeptDet = data => {
  let path = `/sys/org/detail`
  return req.post(path, data)
}
//查询党组织分页
export const queryPartyDeptPage = data => {
  let path = `/sys/org/selectAll`
  return req.post(path, data)
}
//查询下级党组织
export const getPartyDeptChild = data => {
  let path = `/sys/org/selectChild`
  return req.post(path, data)
}
//更新组织机构信息
export const updatePartyDeptInfo = data => {
  let path = `/sys/org/update`
  return req.post(path, data)
}
//更新组织机构状态
export const updatePartyDeptStatus = data => {
  let path = `/sys/org/updateStatus`
  return req.post(path, data)
}


//-------------------党员管理----------------------------------------------------//
// 获取党员信息列表分页
export const getPartyMembers = data => {
  let path = `/cloud/partyMember/list`
  return req.post(path, data)
}
// 删除党员
export const delPartyMember = data => {
  let path = `/cloud/partyMember/delete`
  return req.post(path, data)
}
// 获取党员详情
export const getPartyMemberDet = data => {
  let path = `/cloud/partyMember/detail`
  return req.post(path, data)
}
// 新增党员信息
export const addPartyMember = data => {
  let path = `/cloud/partyMember/save`
  return req.post(path, data)
}
// 更新党员信息
export const updatePartyMember = data => {
  let path = `/cloud/partyMember/update`
  return req.post(path, data)
}



