<template>
  <div>
      <template v-if="!item.children||item.children.length === 0">
          <el-menu-item :index="item.menuHref" @click="menuClick(item)">
            <i class="el-icon-menu"></i>
            <span slot="title">{{item.menuName}}</span>
          </el-menu-item>
      </template>

      <el-submenu v-else :index="item.menuHref">
        <template slot="title" >
          <i class="el-icon-menu"></i>
          <span slot="title">{{item.menuName}}</span>
        </template>

        <template v-for="(child, index) in item.children">
          <sidebar-item
            v-if="child.children&&child.children.length>0"
            :item="child"
            :key="'c'+index"/>
          <el-menu-item v-else :key="'b'+index" :index="child.uri" @click="submenuItemClickEvt(child)">
            <i class="el-icon-location"></i>
            <span slot="title">{{child.name}}</span>
          </el-menu-item>
        </template>
      </el-submenu>
  </div>
</template>

<script>
export default {
  name: 'SidebarItem',
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  mounted () {
  },
  methods: {
    menuClick (menu) {
      this.$router.push({path: menu.menuHref})
    },
    submenuItemClickEvt (menu) {
      this.$router.push({path: menu.menuHref})
    }
  }
}
</script>



