export default {
  response2Message (resp) {
    let message = null
    let code = resp.code
    if (code) {
      switch (code) {
        case '200':
          break
        case '400':
          message = '请求参数错误，请检查参数'
          break
        case '403':
          message = '请求参数错误，请检查参数'
          break
        case '404':
          message = '请求错误,未找到该资源'
          break
        case '405':
          message = '请求错误,未找到该资源'
          break
        case '500':
          message = '服务器端出错,请联系管理员'
          break
        case '503':
          message = '服务器端出错,请联系管理员'
          break
        default:
          message = resp.message
          break
      }
    }
    return message
  }
}



