export const clearSessionStorage = _ => {
  window.sessionStorage.clear()
}

export const getUserInfo = async () => {
  return window.sessionStorage.getItem('user_info')
}

export const setUserInfo = data => {
  if (data) {
    window.sessionStorage.setItem('user_info', JSON.stringify(data))
  }
}

export const setUserDeptTree = data => {
  if (data && data instanceof Array) {
    window.sessionStorage.setItem('user_dept_tree', JSON.stringify(data))
  }
}

export const getActiveModuleId = _ => {
  return window.sessionStorage.getItem('active_module_id')
}

export const setActiveModuleId = moduleId => {
  window.sessionStorage.setItem('active_module_id', moduleId)
}

export const setUserRoleList = data => {
  if (data && data instanceof Array) {
    window.sessionStorage.setItem('user_roles', JSON.stringify(data))
  }
}



